import * as qiniu from 'qiniu-js'
import qiniuApi from "@/network/api/qiniu.js"

function guid() {
    return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function (c) {
        var r = Math.random() * 16 | 0,
            v = c == 'x' ? r : (r & 0x3 | 0x8);
        return v.toString(16);
    });
}

let token = '';

let getToken = () => {
    return new Promise((resolve, reject) => {
        if (token) {
            resolve(token);
        } else {
            qiniuApi.token().then(res => {
                token = res.data;
                resolve(token);
            }).catch(res => {
                reject(res);
            });
        }
    });
};

let getFilePath = (extension) => {
    // sys-image/
    // user-image/
    // user-mp4/
    let e = extension.toLowerCase();
    if (/(.*)\.(gif|jpg|jpeg|png)$/.test(e)) { //
        return 'user-image/';
    } else if (/(.*)\.(mp4|rmvb|avi|ts)$/.test(e)) { //
        return 'user-mp4/';
    } else {
        return '';
    }
}

let upload = (obj, next, error, complete) => {

    getToken().then(token => {

        const {
            file
        } = obj;

        let fileName = guid();

        let index = file.name.lastIndexOf('.');
        let ext = index != -1 ? file.name.substr(index) : '';
        let path = getFilePath(ext);
        fileName = path + fileName + (ext ? ext : '');

        // 
        const putExtra = {
            // fname: "", // fname: string
            // params: {}, // params: object
            // mimeType: [] || null // mimeType: null || array null  ["image/png", "image/jpeg", "image/gif"]
        };

        const config = {
            // useCdnDomain: true,
            // region: qiniu.region.z2
        }

        // sdkobservable
        let observable = qiniu.upload(file, fileName, token.token, putExtra, config);
        let subscription = observable.subscribe(next, error, complete);
        return subscription

    }).catch(res => {
        error(res);
    });
}


// token obj  el-upload  object
export default {
    upload
}